// Cloudflare Pages Function - Google Sheets Integration
// Endpoint: /api/sheets
// Bu dosya Google Sheets API ile gerçek veri çekecek

export async function onRequest(context) {
  const { request, env } = context;
  
  const corsHeaders = {
    'Access-Control-Allow-Origin': '*',
    'Access-Control-Allow-Methods': 'GET, OPTIONS',
    'Access-Control-Allow-Headers': 'Content-Type',
    'Content-Type': 'application/json',
  };

  if (request.method === 'OPTIONS') {
    return new Response(null, { headers: corsHeaders });
  }

  try {
    // Environment variables'dan alınacak
    const SPREADSHEET_ID = env.SPREADSHEET_ID || '1Za8CAl0QQmYLAub6AFJUdtiK5Qry1Ono8qyYwE5EWfI';
    const API_KEY = env.GOOGLE_API_KEY; // Cloudflare'de ayarlanacak
    
    if (!API_KEY) {
      return new Response(JSON.stringify({ 
        error: 'Google API Key not configured',
        message: 'Please add GOOGLE_API_KEY to Cloudflare environment variables'
      }), {
        headers: corsHeaders,
        status: 500
      });
    }

    // Google Sheets API v4 - Public sheets için
    const range = 'Sheet1!A1:Z1000'; // İlk 1000 satır
    const url = `https://sheets.googleapis.com/v4/spreadsheets/${SPREADSHEET_ID}/values/${range}?key=${API_KEY}`;
    
    const response = await fetch(url);
    
    if (!response.ok) {
      throw new Error(`Google Sheets API error: ${response.statusText}`);
    }
    
    const data = await response.json();
    
    // Verileri formatla
    const rows = data.values || [];
    if (rows.length === 0) {
      return new Response(JSON.stringify([]), {
        headers: corsHeaders,
        status: 200
      });
    }
    
    // İlk satır başlık, geri kalanı veri
    const headers = rows[0];
    const products = rows.slice(1).map(row => {
      const product = {};
      headers.forEach((header, index) => {
        product[header] = row[index] || '';
      });
      return product;
    });

    return new Response(JSON.stringify(products), {
      headers: corsHeaders,
      status: 200
    });

  } catch (error) {
    return new Response(JSON.stringify({ 
      error: error.message,
      stack: error.stack 
    }), {
      headers: corsHeaders,
      status: 500
    });
  }
}
